* {
    margin: 0;
    box-sizing: border-box;
}

.main,
.endContainer {
    background-color: white;
    height: 100vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    overflow: auto;
}

.main {
    justify-content: end;
}

.selected {
    background-color: #242424;
    color: white;
}

.container>ul>li>button.selected {
    color: white;
}

.container {
    margin: 5%;
    padding: 2%;
    border-radius: 12px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.container>h2 {
    font-size: 3rem;
}

.container>p {
    font-size: 2rem;
    margin: 2%;
    text-align: center;
    width: 80%;
}

.container>ul {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 1rem 0;
    gap: 0.8em;
    list-style: none;
    width: auto;
}

.container>ul>li {
    padding: 1%;
    font-size: 2rem;
    border-radius: 12px;
    border: none;
    text-wrap: nowrap;
    display: flex;
    justify-content: center;
    width: 100%;
}

.container>ul>li>button {
    width: 100%;
    padding: 0.3em 1em;
    box-sizing: border-box;
    display: flex;
    flex-grow: 1;
    justify-content: center;
    font-size: 2rem;
    border-radius: 12px;
    border: none;
    color: #242424
}

@media (hover: hover) {

    .container>ul>li>button:hover,
    .container>button:hover {
        cursor: pointer;
        background-color: #333333;
        color: white;
    }
}


.container>button {
    width: auto;
    margin: 2%;
    padding: 1rem 2rem;
    flex: 1;
    font-size: 2rem;
    border-radius: 12px;
    border: none;
    color: #242424
}

@media (max-width: 1100px) {
    .container>ul>li {
        text-wrap: wrap;
    }

    .container>p {
        width: 100%;
        font-size: 1.4rem;
        overflow: hidden;
    }

    .container>h2 {
        font-size: 2rem;
    }

    .container>ul>li>button,
    .container>button {
        font-size: 1.5rem;
    }

    .container>ul>li>button,
    .container>ul>li,
    .container>ul {
        width: 100%;
    }

    .main {
        height: 100%;
        justify-content: center;
    }

    .container {
        margin: 0;
        margin-top: 15%;
        width: 90%;
        justify-content: center;
        align-items: center;
    }

    .barContainer {
        position: absolute;
        bottom: 0;
    }

    .endContainer>.container>img {
        align-items: center;
        justify-content: center;
        width: 60%;
        aspect-ratio: 1;
    }

    .endContainer {
        height: 100vh;
        display: flex;
    }
}

.barContainer {
    width: 100%;
    height: 1.2em;
    background-color: #242424;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    overflow: hidden;
    position: absolute;
    margin-bottom: 0;
}

.progressBar {
    height: 100%;
    background-color: chartreuse;
}

.endContainer {
    justify-content: center;
}

.endContainer>.container>button {
    text-wrap: nowrap;
}